.contacts {
  padding: 80px 0 120px;
}

.contacts_wrapper {
  max-width: 950px;
  width: 100%;
  margin: 0 auto;
}

.contacts_title {
  font-family: "Inter-SemiBold";
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  text-align: center;
  color: #000000;
}

.contacts_subtitle {
  max-width: 450px;
  width: 100%;
  margin: 0 auto;
  font-family: "Inter-Medium";
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin-bottom: 60px;
}

.contacts_content {
  display: flex;
  justify-content: space-between;
}

.contacts_item {
  margin-bottom: 18px;
}

.contacts_item h5 {
  font-family: "Inter-SemiBold";
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

.contacts_item a {
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

.contacts_content-right {
  max-width: 460px;
  width: 100%;
}

.question_form {
  background-color: #022869;
  border-radius: 10px;
  padding: 45px 35px;
}

.question_form-inner {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.question_form-inner span {
  font-family: "Inter-Medium";
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  margin-bottom: 4px;
}

.question_form-inner input,
.question_form-inner textarea {
  width: 100%;
  height: 40px;
  padding: 0 5px;
  border-radius: 5px;
}

.question_form-inner textarea {
  height: 130px;
  resize: none
}

.question_form-inner {
  input {
    &::placeholder {
      color: #fff;
    }
  }
  textarea {
    &::placeholder {
      color: #fff;
    }
  }
}

.question_form a {
  max-width: 265px;
  width: 100%;
  background-color: #367af5;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-family: "Inter-Medium";
  padding: 10px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.modal .close {
  position: absolute;
  top: 20px;
  right: 20px;
  display: block;
  width: 16px;
  height: 16px;
  cursor: pointer;
  z-index: 2;
  margin: 0;
}

.close {
  display: none;
  font-size: 13px;
  text-align: right;
  margin: 5px 28px;
}

.modal .close::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 16px;
  height: 2px;
  transform: rotate(45deg);
  transform-origin: center;
  background-color: #fff;
  border-radius: 1px;
}

.modal .close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 16px;
  height: 2px;
  transform: rotate(-45deg);
  transform-origin: center;
  background-color: #fff;
  border-radius: 1px;
}

.modal_wrapper {
  max-width: 560px;
  width: 100%;
  background-color: #022869;
  border-radius: 10px;
  text-align: center;
  position: relative;
  padding: 45px;
  margin: 0 10px;
}

.modal_title {
  font-family: "Inter-SemiBold";
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  margin-bottom: 30px;
}

.modal_subtitle {
  text-align: left;
  max-width: 360px;
  width: 100%;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  margin: 0 auto 30px;
}

.modal_btn {
  background-color: #367af5;
  border-radius: 10px;
  max-width: 105px;
  width: 100%;
  padding: 12px;
  font-family: "Inter-Medium";
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  cursor: pointer;
  border: none;
}

.contacts-title-mobile {
  display: none;
}


@media (max-width: 540px) {
  .contacts {
    padding: 60px 0 50px 0;
    .contacts_title {
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 30px;
    }
    .contacts_subtitle {
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      margin-bottom: 30px;
    }
  }
  .contacts_content {
    flex-wrap: wrap;

    .contacts_item {
      .mail-link {
        font-size: 16px;
        line-height: 19px;
      }
      h5 {
        font-size: 16px;
        line-height: 19px;
      }
      a {
        font-size: 22px;
        line-height: 27px;
      }
    }
  }
  .contacts_content-left {
    margin-bottom: 50px;
  }
  .contacts-title-mobile {
    text-align: center;
    margin-bottom: 30px;
    display: block;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
  }
  .contacts_content-right {

    .question_form {
      background-color: transparent;
      padding: 0;

      a {
        max-width: 100%;
      }

      .question_form-inner {
        span {
          display: none;
        }
        input {
          border: 1px solid #000000;
          padding: 16px 10px;

          &::placeholder {
            font-size: 16px;
            line-height: 19px;
            color: #808080;
          }
        }
        textarea {
          border: 1px solid #000000;
          padding: 16px 10px;

          &::placeholder {
            font-size: 16px;
            line-height: 19px;
            color: #808080;
          }
        }
      }
    }
  }
}